import React from 'react';
import { string, shape, bool } from 'prop-types';

import { SignupForm } from '../../../components/SignupForm';
import { TermsLink } from '../../../components/TermsLink';
import { Features } from '../Features';

import styles from './MainBlock.module.scss';

const MainBlock = ({ params, previewing, features }) => (
  <div className={styles.mainBlock__container}>
    <div className={styles.mainBlock__title}>
      <h1
        className='small'
        dangerouslySetInnerHTML={{ __html: params && params.title }}
      />
    </div>

    <div className={styles.mainBlock__grid}>
      <div className={styles.mainBlock__featuresArea}>
        <Features params={features} />
      </div>

      <div className={styles.mainBlock__imageContainer}>
        <div
          className={styles.mainBlock__image}
          style={params && params.bgImage && {
            backgroundImage: `url(${process.env.ASSETS_URL}${params.bgImage})`
          }}
        />
      </div>
    </div>

    <SignupForm
      buttonText={params && params.buttonText}
      disabled={previewing}
      inputPlaceholder={params && params.inputPlaceholder}
    />

    {/* Make sure you don't load a component that could break in Netlify. Check if in preview mode */}
    {params && params.previewMode ? null : params && <TermsLink />}

    <div
      className={styles.mainBlock__image}
      style={params && params.bgImage && {
        backgroundImage: `url(${process.env.ASSETS_URL}${params.bgImage})`
      }}
    />
  </div>
);

MainBlock.propTypes = {
  params: shape({
    title: string,
    description: string,
    inputPlaceholder: string,
    buttonText: string
  }),
  previewing: bool
};

MainBlock.defaultProps = {
  params: {},
  previewing: false
};

export default MainBlock;



