import React from 'react';
import { arrayOf, string, shape, object } from 'prop-types';

import styles from './TrustBlock.module.scss';

const TrustBlock = ({ params }) => (
  <div className={styles.trustBlock__wrapper}>
    <div className={styles.trustBlock__container}>
      <h2 className='extra-small'>{params.title}</h2>

      <div className={styles.trustBlock__items}>
        <h2 className='extra-small'>{params.title}</h2>
        {params.items.map((item, index) => (
          <img src={`${process.env.ASSETS_URL}${item.image}`} key={index} />
        ))}
      </div>
    </div>
  </div>
);

TrustBlock.propTypes = {
  params: shape({
    title: string,
    items: arrayOf(object)
  })
};

TrustBlock.defaultProps = {
  params: {
    title: 'Trusted by',
    items: [
      {
        image: 'logos/landing/datastax.png'
      },
      {
        image: 'logos/landing/newyorklife.png'
      },
      {
        image: 'logos/landing/gradle.png'
      },
      {
        image: 'logos/landing/instartlogic.png'
      }
    ]
  }
};

export default TrustBlock;



